import {forEach} from "../../js/helpers";
import * as FilePond from "filepond";
import FilePondPluginImagePreview from 'filepond-plugin-image-preview';
import FilePondPluginImageExifOrientation from 'filepond-plugin-image-exif-orientation';
import FilePondPluginFileValidateSize from 'filepond-plugin-file-validate-size';
import FilePondPluginImageEdit from "filepond-plugin-image-edit";
import FilePondPluginFileValidateType from 'filepond-plugin-file-validate-type';

import "filepond/dist/filepond.min.css";
import 'filepond-plugin-image-preview/dist/filepond-plugin-image-preview.css';
import "filepond-plugin-image-edit/dist/filepond-plugin-image-edit.css";

// We want to preview images, so we register
// the Image Preview plugin, We also register
// exif orientation (to correct mobile image
// orientation) and size validation, to prevent
// large files from being added
FilePond.registerPlugin(
		FilePondPluginImagePreview,
		FilePondPluginImageExifOrientation,
		FilePondPluginFileValidateSize,
		FilePondPluginImageEdit,
		FilePondPluginFileValidateType
);

forEach(document.querySelectorAll('.filepond'), filepond => {
	let pond = FilePond.create(filepond, {
		acceptedFileTypes: ['image/*'],

		labelIdle: 'Прикрепить фотографии',
		labelInvalidField: 'Есть некорректные файлы',
		labelFileWaitingForSize: 'Идёт расчёт размера файлов',
		labelFileSizeNotAvailable: 'Размер недопустим',
		labelFileLoading: 'Загрузка',
		labelFileLoadError: 'Возникла ошибка при загрузке',
		labelFileProcessing: 'Загрузка',
		labelFileProcessingComplete: 'Загрузка успешна',
		labelFileProcessingAborted: 'Загрузка отменена',
		labelFileProcessingError: 'Возникла ошибка при загрузке',
		labelFileRemoveError: 'Ошибка при удалении',
		labelTapToCancel: 'Нажмите, чтобы выйти',
		labelTapToRetry: 'Попробовать заново',
		labelButtonRemoveItem: 'Удалить',
		labelButtonAbortItemLoad: 'Отмена',
		labelButtonRetryItemLoad: 'Ещё раз',
		labelButtonAbortItemProcessing: 'Отменить'
	});

	document.querySelector('.js_reset-review-form').addEventListener('click', function (e) {
		e.preventDefault();
		pond.removeFiles();
	});
});

